import React from "react";
import { Row, Col, Card, Table, Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import axios from "axios";
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css"; // Import
import QRcode from '../QRcode/QRcode'

import moment from "moment"
import Aux from "../../hoc/_Aux";
var urlbackend = require("../../env/env")();

class Findcolis extends React.Component {
  state = {
    Drivers: [],
    informationdriver: {}
  };

  componentDidMount() {
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Headers': 'Authorization',
        "x-access-token": localStorage.getItem('token'),
        'Authorization': 'Bearer ' + localStorage.getItem('token'),
      },
    };
  
    axios
      .get(urlbackend + "/admin/colis/filter/" + window.location.href.split("/").pop(), config)
      .then((response) => {
        if (response.data) {
          console.log("this Findcolis list", response.data);
          this.setState({ Drivers: response.data.data })
        }
        else {
          this.setState({ Drivers: [] })

        }

      })
      .catch((e) => {
        console.log(e);
      });
  }

  Delette(Driver) {
    console.log("hellodelet", Driver.id);
    confirmAlert({
      title: "Confirm Delete",
      message: "Are you sure to do Delete?",
      buttons: [
        {
          label: "Yes",
          onClick: () => {
            axios
              .delete("http://localhost:5000/api/v1/Drivers/" + Driver.id)
              .then(() => {
                var array = [...this.state.Drivers];
                var index = array.indexOf(Driver);
                if (index !== -1) {
                  array.splice(index, 1);
                  this.setState({ Drivers: array });
                }
              });
          },
        },
        {
          label: "No",
          onClick: null,
        },
      ],
    });
    /*  */
  }
  render() {
    return (
      <Aux>
        <Row>
          <Col>
            <Card>
              <Card.Header>
                <Card.Title as="h5">
               
                </Card.Title>
              </Card.Header>
              <Card.Body>

                <div>

                  <Table striped responsive>
                    <thead>
                      <tr>
                        <th>Num Colis</th>
                        <th>Addresse</th>
                        <th>designation</th>
                        <th>Prix</th>
                        <th>Créé le</th>
                        <th>Modifié le</th>
                        <th>statut</th>
                        <th style={{ width: "10%" }}>Historique</th>

                      </tr>
                    </thead>
                    <tbody>
                      {this.state.Drivers.map((Driver, index) => (

                        <tr key={Driver.id}>
                            <td>
                          {Driver.index_colis} <br/> {Driver.name_complete} <br/>  {Driver.phone_number}
                          </td>
                          <td>
                          {Driver.gouvernorat} <br/> {Driver.city} <br/>  {Driver.address}
                          </td>

                          <td> 
                              nombre de lot {Driver.lot}
                              <br/>
                              {Driver.designation}
                          </td>
                          <td>
                          {Driver.price} <br/> ( {Driver.payment_mode} )
                          </td>
                          <td>
                            {moment(Driver.created_at).format("YYYY-MM-DD HH:mm:ss")}
                          </td>
                          <td>
                            {moment(Driver.updated_at).format("YYYY-MM-DD HH:mm:ss")}
                          </td>
                          <td> {Driver.provider?.username} <br/>  {Driver.provider?.phone_number} </td>
                          <td>
                            <Link to={"/history/colis/" + Driver.id}>
                              <Button
                                variant="success"
                                onClick={() => console.log("not yet")}
                              >
                                {" "}
                                <span className="fa fa-file"></span>
                              </Button>
                            </Link>


                          </td>

                         <td>
                           <QRcode url={Driver.id}></QRcode>
                         </td>

                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </div>

              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Aux>
    );
  }
}

export default Findcolis;
